.button-group {
    position: relative;
display: inline-flex;
vertical-align: middle;
}

.chart-container {
   margin: 0 auto;
}

.chart-btn {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.3rem 0.5rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    color: #fff;
    background-color: #7896d6;
    border-color: #7896d6;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    margin: 20px 1px;
    cursor: pointer
}

.percent-change-display {
    font-size: 1.3rem;
    text-align: center;
}

.market-rank {
    background-color: black;
    color: white;
    padding: 4px 6px;
}

.name-div {
    display: flex;
    margin: 5px 0;
    align-items: center;
    transform: translateX(-20px)
}

.coin-image {
    border-radius: 50%;
    width: 50px;
    margin: 10px;
}

.crypto-name {
    font-size: 1.6rem;
    font-size: bold;
}

.c-info-symbol {
    font-size: 1.3rem;
    color: rgb(87, 87, 87);
    margin: 0 5px;
}

.progress-bar {
    min-width: 400px;
}

.current-price {
    font-size: 1.55rem;
}

.price-change-24 {
    font-size: 1.2rem;
    margin-left: 5px;
}

.crypto-info-container {
    padding: 4rem;
    width: 100%;
    display: flex;
    justify-content: flex-start;
}

.grid-box {
    display: flex;
    flex-direction: column;
    margin-top: 25px;
}

.grid-box > span {
    border-bottom: 1px solid rgba(211, 211, 211, 0.65);
    padding: 12px 0 ;
    font-size: 1.1rem;
}

.converter-div {
    display: flex;
    align-items: baseline;
    margin: 20px 0;
    justify-content: center;
}

.convert-input {
    display: flex;
    align-items: center;
}

.input {
    width: 120px;
    height: 32px;
    margin: 0 2px ;
}

.fa-exchange {
    margin: 0 10px;
}

.active-btn {
    background-color: #3459a1 !important;
}
@media screen and (max-width: 1200px) {
    .crypto-info-container {
        max-width: 1140px;
    }
}
@media screen and (max-width: 970px) {
    .crypto-info-container {
        flex-direction: column;
        padding:20px;
        max-width: 940px
    }
}

@media screen and (max-width: 768px) {
    .crypto-info-container {
    max-width: 720px;
    }
}


@media screen and (max-width: 550px) {
     #highcharts-ri6507i-0 {
     width: 400px !important;
        margin: 0;
    }
